<template>
    <!--begin::Modal-->  
    <div class="modal fade" id="userModal" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" role="dialog" aria-labelledby="staticBackdropLabel" aria-hidden="true">
        <div class="modal-dialog modal-dialog-centered" role="document">
            <form @submit.prevent="form.id ? update() : store()" class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title" id="formModalLabel" >{{ form.id ? 'تحديث الصنف':'إضافة صنف جديد' }}</h5>
                    <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close">
                    </button>
                </div>
                <div class="modal-body row"> 
                    <div class="col-12 mt-3">
                        <label >الأسم</label>
                        <input v-model="form.name" type="text" autocomplete="off"
                        class="form-control" :class="{ 'is-invalid': form.errors.has('name') }">
                        <has-error :form="form" field="name"></has-error>
                    </div> 
                    <div class="col-6 mt-3">
                        <label >التليفون</label>
                        <input v-model="form.phone" type="text" autocomplete="off"
                        class="form-control" :class="{ 'is-invalid': form.errors.has('phone') }">
                        <has-error :form="form" field="phone"></has-error>
                    </div> 
                    <div class="col-6 mt-3">
                        <label :class="`font-${$i18n.locale}`">الوظيفة</label>
                        <v-select v-if="roles" dir="rtl" label="name" class="vselect" style="width: 100%;"
                                    :options="roles" :reduce="val => val.id" v-model="form.role"></v-select> 
                        <has-error :form="form" field="role"></has-error>
                    </div> 
                    <div class="col-6 mt-3">
                        <label :class="`font-${$i18n.locale}`">الفرع التابع له</label>
                        <v-select v-if="stores" dir="rtl" label="name" class="vselect" style="width: 100%;"
                                    :options="stores" :reduce="val => val.id" v-model="form.store_id"></v-select> 
                        <has-error :form="form" field="store_id"></has-error>
                    </div> 
                    <div class="col-6 mt-3">
                        <label >كلمة المرور</label>
                        <input v-model="form.password" type="password" autocomplete="off"
                        class="form-control" :class="{ 'is-invalid': form.errors.has('password') }">
                        <has-error :form="form" field="password"></has-error>
                    </div> 
                </div> 
                <div class="modal-footer">
                    <button type="button" class="btn btn-light" data-bs-dismiss="modal" >إغلاق</button>
                    <button v-if="form.id" type="submit" class="btn btn-primary" >تحديث</button>
                    <button v-else type="submit" class="btn btn-primary" >حفظ</button>
                </div>
            </form>
        </div>
    </div>
    <!--end::Modal-->
</template>

<script>
import {Form,HasError} from 'vform'
import axios from 'axios';
import "vue-select/dist/vue-select.css";


export default {
    components: {HasError},
    props: ['fetchData'],
    data() {
        return {
            form: new Form({id:null,name:null,phone:null,role:null,password:null,store_id:null}),
            roles: [
                {id:'admin',name:'Admin'},
                {id:'sales',name:'Sales'},
                {id:'accountant',name:'Accountant'},
            ],
            stores:null,
        };
    }, 
    created() {
        this.getStores();
    },
    methods: {
        getStores(){
            axios.get('/api/stores',{params: {paginate:0,filter:{}}}).then(({data}) => {
                this.stores=data.success.stores;
            }).catch((error) => {console.log("Error......")})
        },
        create(user_id){
            this.form.reset();
            $('#userModal').modal('show');
        },
        store(){ 
            this.form.post("/api/users").then(({data}) => {
                if (data.message) {
                    this.$swal.fire({icon: 'error',title: data.company.name,text: data.message+'!',
                        footer: '<a href>Why do I have this issue?</a>'
                    });
                } else {
                    this.fetchData();
                    $('#userModal').modal('hide');
                }
            }).catch((error) => {console.log("Error......")})
        },  
        edit(form){ 
            this.form.reset();
            this.form.fill(form);
            $('#userModal').modal('show');
        },
        update(){
            this.form.put("/api/users/"+this.form.id).then(({data}) => {
                this.fetchData();
                $('#userModal').modal('hide');
            }).catch((error) => {console.log("Error......")})
        },  
        destroy(id) {
            this.$swal.fire({
                title:this.$t("modals.general.warning"),text:this.$t("modals.general.r_u_s"),
                icon: 'warning',showCancelButton: true,confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',cancelButtonText: this.$t("modals.general.cancel"),
                confirmButtonText: this.$t("modals.general.yes_delete"),
            }).then((result) => {
                if (result.value) {
                    axios.delete('/api/users/'+id).then(response => {
                        if(response.data.message){
                            this.$swal.fire({icon: 'error',title: 'Oops...',text: response.data.message+'!',
                                footer: '<a href>Why do I have this issue?</a>'
                            });
                        }else{
                            this.$swal.fire(this.$t("modals.general.deleted"),this.$t("modals.general.delete_success"),'success');
                            this.fetchData();
                        }
                    }).catch(() => {
                        this.$swal.fire({icon: 'error',title: 'Oops...',text: 'Something went wrong!',
                            footer: '<a href>Why do I have this issue?</a>'
                        }); 
                    });
                }
            })
        },
    },
}
</script>
